<template>
  <el-dialog
    :class="['portal-dialog', customFooter ? 'custom-footer' : '', subtitle ? 'has-subtitle' : '']"
    :visible.sync="dialogVisible"
    :title="title"
    :width="width"
    v-bind="$attrs"
    v-on="$listeners"
    :close-on-click-modal="closeOnClickModal"
    :show-close="showClose"
    :before-close="handleClose"
    @opened="openedDialog"
    @closed="closedDialog"
  >
    <span v-if="subtitle" class="subtitle">{{ subtitle }}</span>
    <slot name="title"></slot>
    <slot name="content"></slot>
    <slot name="footer" class="custom-fotter" v-if="customFooter"></slot>
    <div slot="footer" v-else>
      <button
        v-if="showCancel"
        :class="['cancel-button', cancelBtnDisable ? 'disabled' : '']"
        @click="handleCancelClick"
      >
        {{ cancelText }}
      </button>
      <el-button
        type="primary"
        v-if="showSubmit"
        :disabled="confirmBtnDisable"
        :loading="submitLoading"
        size="small"
        @click="handleConfirmClick"
        >{{ confirmText }}</el-button
      >
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'PortalDialog',
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: '',
    },
    width: {
      type: String,
      default: '448px',
    },
    customFooter: {
      type: Boolean,
      default: false,
    },
    cancelText: {
      type: String,
      default: 'Cancel',
    },
    confirmText: {
      type: String,
      default: 'Confirm',
    },
    closeOnClickModal: {
      type: Boolean,
      default: false,
    },
    showClose: {
      type: Boolean,
      default: true,
    },
    cancelBtnDisable: {
      type: Boolean,
      default: false,
    },
    confirmBtnDisable: {
      type: Boolean,
      default: false,
    },
    showSubmit: {
      type: Boolean,
      default: true,
    },
    subtitle: {
      type: String,
      default: '',
    },
    showCancel: {
      type: Boolean,
      default: true,
    },
    submitLoading: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      dialogVisible: false,
    };
  },
  watch: {
    visible: {
      handler(value) {
        this.dialogVisible = value;
      },
      immediate: true,
    },
    dialogVisible: {
      handler(newVal) {
        this.$emit('show-dialog', newVal);
        this.$emit('update:visible', newVal);
      },
    },
  },
  methods: {
    handleClose() {
      this.dialogVisible = false;
      this.$emit('handle-close');
    },
    handleCancelClick() {
      this.$emit('cancel-btn-click');
    },
    handleConfirmClick() {
      this.$emit('confirm-btn-click');
    },
    openedDialog() {
      this.$emit('opened-dialog');
    },
    closedDialog() {
      this.$emit('closed-dialog');
    },
  },
};
</script>

<style lang="scss" scoped>
.portal-dialog {
  &.custom-footer {
    /deep/ .el-dialog {
      .el-dialog__body {
        padding: 0 24px 24px 24px;
      }
    }
  }
  &.has-subtitle {
    /deep/ .el-dialog {
      .el-dialog__header {
        padding: 24px 24px 8px 24px;
      }
      .el-dialog__body {
        .subtitle {
          font-size: 14px;
          color: $--text_color--secondary;
          word-wrap: break-word;
        }
      }
    }
  }
  /deep/ .el-dialog {
    border-radius: 8px;
    button {
      font-size: 14px;
    }
    .el-dialog__header {
      padding: 24px 24px 20px 24px;
      .el-dialog__title {
        font-size: 20px;
        line-height: 27px;
        font-weight: bold;
        font-family: HuaweiSans-Bold;
        color: $--text_color--emphasis;
      }
      .el-dialog__headerbtn {
        top: 16px;
        right: 16px;

        .el-dialog__close {
          font-size: 16px;
        }
      }
    }
    .el-dialog__footer {
      padding: 20px 24px 24px 24px;
      margin: 0px;
      .el-button--primary {
        background-color: $--primary_color;
        border-color: $--primary_color;
      }
      .el-button--primary.is-disabled {
        background-color: #f7f8fa;
        border-color: #f7f8fa;
        color: #c9cdd4;
      }
    }
    .el-dialog__body {
      padding: 0px 24px;
      margin: 0px;
    }
  }
  /deep/ .el-button--small {
    padding: 7px 15px;
    height: 32px;
  }
  .cancel-button {
    padding: 7px 15px;
    height: 32px;
    border-radius: 4px;
    background-color: #ffffff;
    border: 1px solid #e5e6eb;
    font-size: 14px;
    color: $--fill_color--emphasis;
    box-sizing: border-box;
    cursor: pointer;
    margin-right: 8px;
    &.disabled {
      background-color: #f7f8fa;
      border-color: #f7f8fa;
      color: #c9cdd4;
    }
  }
}
</style>
