/**
 * 获取字符串校验对象
 * @param {minLen, maxLen} 最小，最大长度
 */
export function getStrLenValidateObj({ minLen = 0, maxLen = 100 }) {
  let msg = '';
  if (minLen > 0) {
    msg = `Must be between ${minLen} and ${maxLen} characters`;
  } else {
    msg = `Must be less than ${maxLen} characters`;
  }
  return {
    min: minLen,
    max: maxLen,
    message: msg,
    trigger: 'blur',
  };
}

export const len4to0trValidateObj = getStrLenValidateObj({
  minLen: 0,
  maxLen: 4,
});

// 将路径表达式转换取值方法
export function parsePath(path) {
  const arr = path.split('.');
  return function (data) {
    for (const key of arr) {
      if (!data) {
        return;
      }
      data = data[key];
    }
    return data;
  };
}

// 校验模糊查询是否输入了内容
export function checkFuzzySearchInput(obj) {
  let arr = [];
  for (let item in obj) {
    if ((obj[item] && obj[item].toString().startsWith('%') && obj[item].length === 1) || !obj[item]) {
      arr.push(false);
    } else {
      arr.push(true);
    }
  }
  if (arr.every((item) => item === false)) {
    return false;
  }
  return true;
}

export function checkLengthExceptPercentSign(errorMsg) {
  return {
    validator: (rule, value, callback) => {
      if (value && value.startsWith('%') && value.length === 1) {
        callback(new Error(`${errorMsg}`));
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
}

export function checkmsisdnLengthExceptPercentSign() {
  return {
    validator: (rule, value, callback) => {
      if (value && value.startsWith('%') && value.length <= 4) {
        callback(new Error('The MSISDN must contain at least 4 digits.'));
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
}
