<template>
  <div id="app" class="login-wrapper">
    <div class="wrapper-left">
      <div class="login-animation"></div>
    </div>
    <!--  登陆部分   -->
    <div class="wrapper-right">
      <el-row>
        <el-col :span="12">
          <div class="login-content">
            <div class="login-title">
              <p class="huaweiSans_medium">{{ $i18n.t('auth.welcomeTitle') }}</p>
              <p class="huaweiSans_bold">{{ $i18n.t('auth.login') }}</p>
            </div>

            <div v-if="loginErrorMsg.length > 0" class="error-tip-content">
              {{ loginErrorMsg }}
            </div>

            <div class="login-form">
              <el-form
                id="login-form"
                :model="loginForm"
                ref="loginForm"
                :rules="loginRule"
                :style="{ 'padding-top': loginErrorMsg ? '32px' : '16px' }"
              >
                <el-form-item prop="shortCode" :label="$i18n.t('auth.shortCode')">
                  <el-input
                    id="shortCode"
                    size="small"
                    type="text"
                    v-model="loginForm.shortCode"
                    placeholder="Please enter"
                    @keyup.native.enter="nextFocus($event)"
                    @focus="initInputDom()"
                  />
                </el-form-item>

                <el-form-item prop="operatorName" :label="$i18n.t('auth.userName')">
                  <el-input
                    id="userAccount"
                    size="small"
                    v-model.trim="loginForm.operatorName"
                    @keyup.native.enter="nextFocus($event)"
                    placeholder="Please enter"
                    @focus="initInputDom()"
                  />
                </el-form-item>

                <el-form-item prop="password" :label="$i18n.t('auth.password')">
                  <el-input
                    id="password"
                    size="small"
                    type="password"
                    v-model="loginForm.password"
                    placeholder="Please enter"
                    @keyup.native.enter="nextFocus($event)"
                    @focus="initInputDom()"
                  />
                </el-form-item>

                <el-form-item
                  v-if="needGraphicVerifyCode"
                  prop="graphicVerifyCode"
                  :label="$i18n.t('auth.verifyCode')"
                  class="verify-code-input"
                >
                  <el-input
                    id="verifyCode"
                    size="small"
                    v-model="loginForm.graphicVerifyCode"
                    placeholder="Please enter"
                    @keyup.native.enter="nextFocus($event)"
                    @focus="initInputDom()"
                  >
                    <img
                      slot="append"
                      ref="loginVerifyCodeRef"
                      :src="initLoginVCUrl"
                      @click="refreshLoginVerifyCodeImage"
                      class="verifycode-img"
                    />
                  </el-input>
                </el-form-item>
              </el-form>

              <div class="agreement-btn">
                <el-checkbox :disabled="checkboxDisabled" v-model="agreementChecked">
                  <span>{{ $i18n.t('auth.agreeTo') }}</span>
                  <el-button type="text" @click="dialogVisible = true">{{ $i18n.t('auth.loginServer') }}</el-button>
                </el-checkbox>
              </div>

              <el-button
                id="loginBtn"
                :disabled="!checkFormIptLth"
                class="login-btn"
                type="primary"
                @click="submitForm('loginForm')"
                :loading="loginLoading"
              >
                {{ $i18n.t('auth.login') }}
                <i class="el-icon-right el-icon--right"></i>
              </el-button>

              <el-button type="text" class="forget-text-btn" @click="toForgetPwd">
                {{ $i18n.t('auth.forgetPassword') }}
              </el-button>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>

    <!-- 用户协议弹窗 -->
    <portal-dialog
      width="584px"
      :visible="dialogVisible"
      :customFooter="true"
      :showClose="true"
      @opened-dialog="opendDialog"
      @closed-dialog="closedDialog"
      :title="agreementInfo.title"
    >
      <div slot="content" class="agreement-wrapper">
        <div>
          <p class="agreement-content">{{ agreementInfo.content }}</p>
        </div>
      </div>
      <div slot="footer" class="agreement-footer">
        <el-button size="small" type="primary" :disabled="closeBtnDisabled" @click="agreementBtn">
          {{ $i18n.t('auth.agreeAndRead') }}<span v-if="showCountDown">({{ timeToClose }}S)</span>
        </el-button>
      </div>
    </portal-dialog>

    <!-- 是否修改密码弹窗 -->
    <portal-dialog
      :customFooter="true"
      @closed-dialog="cancelChangePwdAndGo2Main"
      :visible="isChangePwdVisible"
      :title="$t('auth.expireTitle')"
    >
      <div slot="content" class="is-change-pwd-content">
        <span class="subtitle">{{ $t('auth.expireSubTitle') }}</span>
      </div>
      <div slot="footer" class="change-pwd-footer">
        <el-button size="small" type="text" @click="toChangePassword">{{ $t('auth.clickToChangePwd') }}</el-button>
      </div>
    </portal-dialog>
  </div>
</template>

<script>
import { querySumInitParams, authenticateLogin, getInitLoginVCImg, getServiceAgreement, queryQuestions } from '../api';
import { len4to0trValidateObj } from '@/common/validator/common';
import PortalDialog from '@/components/portal-dialog';
import { resetRouter } from '@/router';
import cookie from '@/common/utils/cookie';

export default {
  name: 'Login',
  components: { PortalDialog },
  data() {
    return {
      loginLoading: false,
      loginErrorMsg: '',
      loginForm: {
        operatorName: '',
        password: '',
        graphicVerifyCode: '',
        shortCode: '',
      },
      initLoginVCUrl: '',
      loginRule: {
        operatorName: [],
        password: [],
        shortCode: [],
        graphicVerifyCode: [],
      },
      needGraphicVerifyCode: false,
      inputDoms: [],
      graphicVerifyCodeKey: '',
      agreementChecked: false,
      dialogVisible: false,
      agreementInfo: {
        title: '',
        content: '',
      },
      timeToClose: 3,
      closeBtnDisabled: true,
      showCountDown: true,
      checkboxDisabled: true,
      countDownEvent: null,
      isChangePwdVisible: false,
      initiatorIdentityId: '',
    };
  },
  computed: {
    checkFormIptLth() {
      let lengthExit = false;
      let allEnterValue =
        this.loginForm['operatorName'].length > 0 &&
        this.loginForm['password'].length > 0 &&
        this.loginForm['shortCode'].length > 0 &&
        this.agreementChecked === true;
      if (this.needGraphicVerifyCode) {
        if (allEnterValue && this.loginForm['graphicVerifyCode'].length > 0) {
          lengthExit = true;
        }
      } else {
        if (allEnterValue) {
          lengthExit = true;
        }
      }
      return lengthExit;
    },
  },
  created() {
    resetRouter();
    querySumInitParams().then((res) => {
      if (res.needGraphicVerifyCode) {
        this.needGraphicVerifyCode = res.needGraphicVerifyCode;
        this.loginRule.graphicVerifyCode = this.needGraphicVerifyCode ? [len4to0trValidateObj] : [];
      }
    });
    this.refreshLoginVerifyCodeImage();
    getServiceAgreement().then((res) => {
      if (res && res.header.success) {
        this.agreementInfo.title = res.title;
        this.agreementInfo.content = res.content;
      }
    });
  },
  mounted() {},
  methods: {
    cancelChangePwdAndGo2Main() {
      this.go2MainMenu();
    },

    // 即将过期跳转修改密码
    toChangePassword() {
      this.$router.push({
        path: '/forceChangePwd',
        query: {
          operatorName: this.loginForm.operatorName,
          initiatorIdentityId: this.initiatorIdentityId,
          shortCode: this.loginForm.shortCode,
          expireFlag: 'aboutToExpire',
        },
      });
    },

    toForgetPwd() {
      this.$router.push({
        path: '/forgetPwd',
      });
    },
    agreementBtn() {
      this.dialogVisible = false;
      this.agreementChecked = true;
      this.timeToClose = 3;
    },

    closedDialog() {
      this.dialogVisible = false;
      clearInterval(this.countDownEvent);
    },

    // 用户协议弹窗打开事件
    opendDialog() {
      this.timeToClose = 3;
      this.countDownEvent = setInterval(() => {
        this.timeToClose--;
        if (this.timeToClose === 0) {
          this.closeBtnDisabled = false;
          this.showCountDown = false;
          this.checkboxDisabled = false;
          clearInterval(this.countDownEvent);
        }
      }, 1000);
    },

    refreshLoginVerifyCodeImage() {
      getInitLoginVCImg().then((res) => {
        if (res) {
          this.initLoginVCUrl = res.graphicVerifyCode;
          this.graphicVerifyCodeKey = res.graphicVerifyCodeKey;
        }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (!this.agreementChecked) {
          return;
        }
        if (valid) {
          if (formName === 'loginForm') {
            this.login();
          }
        } else {
          return false;
        }
      });
    },

    // 登陆
    async login() {
      this.loginErrorMsg = '';
      this.loginLoading = true;
      let loginParam = {
        password: this.loginForm.password,
        graphicVerifyCode: this.loginForm.graphicVerifyCode,
        shortCode: this.loginForm.shortCode,
        graphicVerifyCodeKey: this.graphicVerifyCodeKey,
        operatorName: this.loginForm.operatorName,
      };
      try {
        const res = await authenticateLogin(loginParam);
        if (res.header.success === true) {
          this.initiatorIdentityId = res.orgOperatorIdentityId;
          cookie.set('initiatorIdentityId', res.orgOperatorIdentityId);
          cookie.set('initiatorShortCode', res.shortCode);
          cookie.set('operator_name', res.operatorName);
          let queryParams = {
            operatorName: this.loginForm.operatorName,
            shortCode: this.loginForm.shortCode,
            initiatorIdentityId: res.orgOperatorIdentityId,
          };
          if (res.redirectFlag === 'FLFM') {
            // 首次登录需要修改密码
            const queryRes = await queryQuestions();
            if (queryRes.header && queryRes.header.success) {
              let query = {};
              const { orgOperatorQuestionAnswer, questionNumber } = queryRes;
              if (questionNumber > 0 && orgOperatorQuestionAnswer.length > 0) {
                query = {
                  ...queryParams,
                  receiverIdentityId: res.receiverIdentityId,
                };
              } else {
                query = {
                  ...queryParams,
                  receiverIdentityId: res.receiverIdentityId,
                  redirectFlag: 'changeWidthoutQA',
                };
              }
              this.$router.push({
                path: '/firstLoginChangePwd',
                query: query,
              });
            }
          } else if (res.redirectFlag === 'FLFMQA') {
            // 忘记密码后修改密码，得到初始密码后需修改密码
            this.$router.push({
              path: '/forceChangePwd',
              query: {
                ...queryParams,
                redirectFlag: 'FLFMQAorPEFM',
              },
            });
          } else if (res.redirectFlag === 'PETM') {
            // 密码即将过期，可选修改密码
            this.isChangePwdVisible = true;
          } else if (res.redirectFlag === 'PEFM') {
            // 密码已过期，强制修改密码
            this.$message.warning('The password has expired. Please change it.');
            this.$router.push({
              path: '/forceChangePwd',
              query: {
                ...queryParams,
                redirectFlag: 'FLFMQAorPEFM',
                expireFlag: 'hasExpire',
              },
            });
          } else {
            this.go2MainMenu(res);
          }
        } else {
          let errMsg = res.header.responseDesc;
          this.processLoginError(errMsg);
        }
      } finally {
        this.loginLoading = false;
      }
    },
    go2MainMenu(res) {
      this.$store.dispatch('routerStore/setTopMenus').then(() => {
        this.$store.dispatch('baseStore/setCurrentUser', res.operatorName);
        const parentQuery = this.$route.query;
        const redirectPath = parentQuery && parentQuery.redirectPath ? parentQuery.redirectPath : '/home';
        this.$router.push({
          path: redirectPath === '/login' ? '/home' : redirectPath,
        });
      });
    },
    processLoginError(errorMsg) {
      let verCodeMsg = 'SM.LOGIN.LABEL.VCODE_INVALID';
      if (errorMsg == verCodeMsg) {
        this.loginErrorMsg = 'Incorrect verification code.';
      } else {
        this.loginErrorMsg = errorMsg;
      }
      if (this.loginErrorMsg === undefined) {
        this.loginErrorMsg = 'System Error';
      }
      if (this.needGraphicVerifyCode) {
        this.refreshLoginVerifyCodeImage();
      }
    },
    initInputDom() {
      if (this.inputDoms.length != 0) {
        return;
      }
      let inputDoms = document.querySelectorAll('#login-form .el-input__inner');
      inputDoms.forEach((item, index) => {
        item.setAttribute('data-index', index);
      });
      this.inputDoms = inputDoms;
    },
    nextFocus(event) {
      let index = event.target.getAttribute('data-index');
      let nextIndex = parseInt(index) + 1;
      let length = this.inputDoms.length;
      if (nextIndex < length) {
        this.inputDoms[nextIndex].focus();
        return;
      }
      if (!this.loginLoading) {
        this.submitForm('loginForm');
      }
    },
  },
};
</script>

<style scoped lang="scss">
