    align-items: center;
  }
  .wrapper-right {
    flex: 1;
    display: flex;
    justify-content: center;
    background-color: #fff;
    .el-row {
      flex: 1;
      align-self: center;
      display: flex;
      justify-content: center;
    }
    .login-content {
      max-width: 480px;
      margin: 0 auto;
      align-self: center;
      .login-form {
        .el-form-item {
          margin-bottom: 14px;
        }

        /deep/ .el-form {
          .verify-code-input {
            .el-input-group--append {
              .el-input__inner {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
              }
            }
          }
          .el-form-item {
            .el-input__inner {
              color: $--text_color--emphasis;
            }
            .el-form-item__label {
              margin-left: 0;
            }
          }
        }
        /deep/ .el-form-item:last-child {
          margin-bottom: 8px;
        }
        /deep/.el-form-item__label {
          font-size: 14px;
          line-height: 26px;
          color: $--text_color--strong;
        }
        /deep/ .el-input-group__append {
          line-height: normal;
          background-color: #fff;
          padding: 0px;
          .verifycode-img {
            height: 28px;
            margin-top: 0px;
            display: inherit;
          }
        }
        /deep/ .el-form-item__label:before {
          content: '';
        }

        .agreement-btn {
          font-size: 14px;
          margin: 20px 0;
          margin-top: 16px;
          /deep/ .el-checkbox__inner {
            border-color: $--fill_color--dark;
          }
          /deep/ .el-checkbox__input.is-disabled .el-checkbox__inner {
            background-color: transparent;
          }
          span {
