          }
        }
        /deep/ .el-checkbox__input.is-checked .el-checkbox__inner {
          background-color: $--primary_color;
          border-color: $--primary_color;
        }

        .forget-text-btn {
          font-size: 14px;
          color: $--fill_color--emphasis;
          margin-left: 0px;
          margin-top: 12px;
        }
      }
      .error-tip-content {
        width: 100%;
        color: #f53f3f;
        background-color: rgba(255, 242, 240, 0.5);
        border-radius: 4px;
        line-height: 24px;
        padding-left: 12px;
      }
      .login-title {
        p {
          margin: 4px 0;
          color: $--text_color--emphasis;
        }
        p:nth-of-type(1) {
          font-size: 20px;
          color: $--text_color--strong;
          margin-bottom: 14px;
        }
        p:nth-of-type(2) {
          font-size: 36px;
          margin-bottom: 8px;
        }
      }
      .login-btn {
        width: 100%;
        height: 40px;
        font-size: 16px;
        .el-icon--right {
          float: right;
          position: relative;
          top: -2px;
        }
      }
    }
  }
  /deep/ .el-dialog__wrapper {
    padding-left: 0;
  }
  .portal-dialog {
    /deep/ .el-dialog {
      .el-dialog__body {
        padding-right: 12px;
      }
    }
    .agreement-footer {
      text-align: center;
      margin-top: 28px;
    }
    .agreement-wrapper {
      height: 480px;
      overflow: auto;
      &::-webkit-scrollbar {
        width: 6px;
        color: #c9cdd4;
      }
    }
    .agreement-content {
      line-height: 30px;
      color: $--text_color--strong;
      padding-bottom: 18px;
      padding-right: 12px;
